.section.masters {
  background: #f7f7f7;
  padding-top: 84px;
  padding-bottom: 84px;
  @media screen and (min-width: 1200px) {
    padding-top: 130px;
    padding-bottom: 130px;
  }
  & .section-name {
    text-align: left;
    margin-top: 20px;
    margin-bottom: 0px;
    font-family: Raleway, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 1.08em;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #303030;
  }
}
.section-frases {
  display: inline;
  position: relative;
  margin-left: 81px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36em;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #303030;
  &::before {
    content: '';

    position: absolute;
    width: 60px;
    top: 50%;
    transform: translateY(-50%);
    left: -80px;
    // -80рх по тому что длинна этой полоски 60рх + 20рх отступ от текста

    border-top: 1px solid;
    border-color: inherit;
  }
}

.master-list {
  margin-top: 44px;
}
@media screen and (min-width: 1200px) {
  .master-list {
    display: flex;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1119px) {
  .master-list .team:not(:last-child) {
    margin-bottom: 64px;
  }
}
@media screen and (min-width: 1200px) {
  .master-list .team:not(:last-child) {
    margin-right: 30px;
    margin-bottom: 0px;
  }
}
@media screen and (min-width: 1200px) {
  .master-list .team {
    width: calc((100% - (30px * 2)) / 3);
  }
}
.link-decoration {
  fill: #303030;
  background-color: #ffffff;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  justify-content: center;
  display: flex;
  align-items: center;

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    fill: #ff6c00;
  }
}
.link-social {
  display: flex;
  margin-top: 30px;
  justify-content: center;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  & .item-heroes + .item-heroes {
    margin-left: 10px;
  }
}
.master-list {
  & .heroes-name {
    margin-top: 8px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.36em;

    text-align: center;
    letter-spacing: 0.02em;

    color: #ff6c00;
  }
  & .section-heroes {
    margin-top: 30px;
    margin-bottom: 0;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 1.4em;

    text-align: center;
    letter-spacing: 0.02em;

    color: #303030;
  }
}
.master-list img {
  display: block;
  width: 100%;
}
